@import url(https://fonts.googleapis.com/css?family=Monoton|Lora|Montserrat:600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Pacifico&family=Source+Sans+Pro:wght@200;300;400;600;700;900&display=swap);
.hero{height:100%;display:flex;align-items:center;justify-content:center;overflow:hidden;position:relative;background-color:black}.hero .posis{height:100%;display:flex;flex-direction:column;align-items:center;justify-content:center}.hero .posis .mouse{height:45px;width:30px;background-color:#f4f4fd;border-radius:20px;display:flex;justify-content:center;padding:10px 0;margin-top:auto;position:absolute;left:calc(50% - 15px);bottom:2em;cursor:pointer}.hero .posis .mouse .ball{transition:all 0.4s ease-in-out;height:8px;width:8px;border-radius:50%;background-color:#1b1b1b;display:block;-webkit-animation-delay:2s;animation-delay:2s;-webkit-animation-timing-function:ease-in-out;animation-timing-function:ease-in-out}.hero .posis .mouse:hover .ball{transform:translateY(1em)}.hero .content{display:flex;flex-direction:column;align-items:center;justify-content:center;text-align:center}.hero .content .intro{color:#f4f4fd;font-size:2em;font-weight:100;position:relative}.hero .content .intro::before{position:absolute;content:"HI THERE, I'M";font-weight:600;letter-spacing:0.1em;font-size:1em;white-space:nowrap;text-align:center;top:0;left:0;right:0;color:#f4f4fd}.hero .content .intro .name{background:linear-gradient(#006eff, #00a8ff, #0e63d1);-webkit-background-clip:text;background-clip:text;-webkit-text-fill-color:transparent;font-family:"Pacifico";font-size:4em !important;line-height:2em;display:block}@media (max-width: 600px){.hero .content .intro .name{font-size:2.5em !important}}.hero .content p{margin-top:-1em;color:#fff;font-size:1.8em;font-weight:600}@media (max-width: 600px){.hero .content p{font-size:17px}}

.buger-menu{position:fixed;top:2em;left:1em;width:0vw;height:0vh;overflow:hidden;background:linear-gradient(#006eff, #00a8ff, #0e63d1);transition:all 0.3s ease-in-out;z-index:2;border-radius:50%;border-top-left-radius:0;display:flex;display:none}@media (max-width: 850px){.buger-menu{display:flex}}.buger-menu .buger-menu-links{margin:auto;display:flex;flex-direction:column;align-items:center}.buger-menu .buger-menu-links .buger-menu-option{padding:0.5em 1em;margin:0.5em 0;color:#f4f4fd;margin:0 1em;padding:1em;cursor:pointer;font-weight:500;display:flex;align-items:center;justify-content:center}.buger-menu .buger-menu-links .buger-menu-option span{font-weight:600;color:white}.buger-menu .buger-menu-links .buger-menu-option img{height:1.5em;width:1.5em;margin-bottom:0.5em;margin-right:0.4em}.show{width:100vw;height:100vh;top:0;left:0;border-radius:0}.hide{width:0vw;height:0vh;top:2em;left:1em;border-radius:50%;border-top-left-radius:0}

.header{position:relative;height:100vh;background:transparent}.header .fancy{background:transparent}.header .navbar,.header .mobo-navbar{position:fixed;top:0;right:0;left:0;z-index:6;padding:2em 0;transition:all 1s;box-shadow:0px 3px 6px #00000027}@media (max-width: 850px){.header .navbar,.header .mobo-navbar{display:none}}.header .navbar .nav-links,.header .mobo-navbar .nav-links{display:flex;align-items:center;justify-content:center}.header .navbar .option,.header .mobo-navbar .option{color:#f4f4fd;margin:0 1em;padding:1em;cursor:pointer;font-weight:500;display:flex;align-items:center;justify-content:center}.header .navbar .option span,.header .mobo-navbar .option span{font-weight:600;color:white}.header .navbar .option img,.header .mobo-navbar .option img{height:1.5em;width:1.5em;margin-bottom:0.5em;margin-right:0.4em}.header .navbar .shop-btn,.header .mobo-navbar .shop-btn{padding:0.5em 1em;border-radius:0.2em;max-width:6.5em;background-image:linear-gradient(#006eff, #00a8ff, #0e63d1)}.header .mobo-navbar{width:100%;display:flex;align-items:center;justify-content:space-between;display:none;background-color:inherit;box-shadow:none}@media (max-width: 850px){.header .mobo-navbar{display:flex}}.header .mobo-navbar .menu{margin:0 1em;padding:0.5em;border-radius:0.2em;max-width:4em;background-image:linear-gradient(#006eff, #00a8ff, #0e63d1);cursor:pointer}.header .mobo-navbar .shop-btn{padding:0.5em 1em;border-radius:0.2em;max-width:6.5em;background-image:linear-gradient(#006eff, #00a8ff, #0e63d1)}@media (max-width: 850px){.header .mobo-navbar .shop-btn{padding:0.5em}.header .mobo-navbar .shop-btn img{margin:0}.header .mobo-navbar .shop-btn span{display:none}}.header .white-bg{background-color:white}.header .white-bg .option{color:#1b1b1b !important}.shop-route{height:15vh}.shop-route .navbar{background:black}

.footer{padding:20px 0 30px 0;text-align:center;color:#1b1b1b}@media (max-width: 600px){.footer .footer img{width:70px}}.footer h4{display:inline-block}.footer img{width:60px}.footer p{color:#1b1b1b;display:inline-block;font-size:1.6em;font-weight:600}.footer p a{color:#006eff;font-weight:500}

.portfolio-nav{width:100%;display:flex;flex-wrap:wrap;align-items:center;justify-content:center;margin-bottom:20px}.portfolio-nav .link{padding:10px;text-transform:uppercase;margin:10px;font-size:1.8em;font-weight:500;line-height:1;white-space:nowrap;color:#1b1b1b;letter-spacing:2px;cursor:pointer}.portfolio-nav .link:first-child{cursor:pointer}@media (max-width: 500px){.portfolio-nav .link{margin:5px;font-size:14px;letter-spacing:1px}}.portfolio-nav .active{background:linear-gradient(#006eff, #00a8ff, #0e63d1);-webkit-background-clip:text;background-clip:text;-webkit-text-fill-color:transparent}

.project-preview{max-width:340.35px;width:100%;max-height:40em;height:100%;padding:1em}.project-preview .pro{border-radius:0.8em;height:100%;width:100%;box-shadow:0px 3px 6px #00000027}.project-preview .pro img{height:100%;width:100%}

.portfolio .project-list{width:80vw;margin:auto;display:flex;flex-wrap:wrap}

.SpinnerOverlay{height:60vh;width:100%;display:flex;justify-content:center;align-items:center}.SpinnerOverlay .SpinnerContainer{display:inline-block;width:50px;height:50px;border:3px solid #0e63d1;border-radius:50%;border-top-color:#00a8ff;animation:spin 1s ease-in-out infinite;-webkit-animation:spin 1s ease-in-out infinite}@keyframes spin{to{-webkit-transform:rotate(360deg)}}@-webkit-keyframes spin{to{-webkit-transform:rotate(360deg)}}

.service-preview{max-width:340.35px;width:100%;padding:1em}.service-preview .pro{border-radius:0.8em;height:100%;width:100%;padding:2em;box-shadow:0px 3px 6px #00000027}.service-preview .pro h3{font-size:2em;font-weight:500;margin-bottom:0.5em}.service-preview .pro p{font-weight:300;color:#1b1b1b;letter-spacing:0.1em}

.services .service-list{padding-top:3em;width:80vw;margin:auto;display:flex;flex-wrap:wrap;justify-content:space-evenly}@media (max-width: 600px){.services .service-list{width:90vw}}

.instagram-posts{width:80vw;min-height:1px;margin:auto;display:flex;flex-wrap:wrap;justify-content:center;position:relative}@media (max-width: 500px){.instagram-posts{width:90vw}}.instagram-posts .post{flex:0 0 25%;max-width:24.5em;padding:1em}.instagram-posts .post img{max-width:24.5em;max-height:24.5em;width:100%;height:100%;box-shadow:0px 3px 6px #00000027}.no-post span{text-align:center;display:block;line-height:5em;font-size:2em;font-weight:600;color:#999}

.contact-details{display:flex;flex-wrap:wrap;justify-content:space-evenly;align-items:center;position:relative;height:20em;width:100%;background-color:#707070}.contact-details p{font-size:2em;font-weight:900;color:white}.contact-details p span{font-weight:500}.contact-details .phone{width:20em}.contact-details .email{width:20em}

.slider{min-height:17em;display:flex}@media (max-width: 500px){.slider{min-height:20em}}.feedback{margin:auto;max-width:60em;padding:0 1em;margin-top:1em;text-align:center}.feedback h2{font-size:1.8em;margin-top:0.5em;font-weight:500}

*{margin:0;padding:0;box-sizing:border-box}h1,h2,h3,h4,h5,h6,p,a,ul,span,li,img{font-weight:300;font-family:"Source Sans Pro", sans-serif}h1,h2,h3,h4,h5,h6{line-height:1.5}p{line-height:1.6;font-weight:400;font-size:15px;color:#adadad}h1{font-size:3.5em;letter-spacing:5px}h2{font-size:2.8em;line-height:1.1}h3{font-size:1.7em}h4{font-size:1.2em;letter-spacing:0.6px}h5{font-size:1.1em}h6{font-size:0.9em;letter-spacing:1px}a,button{font-family:"Montserrat", sans-serif;display:inline-block;text-decoration:none;color:inherit;transition:all 0.3s}a{font-size:15px}.container{width:80vw;margin:auto}.heading{position:relative;margin:4em 0 1em 0;font-size:1.2em;text-align:center}.heading h1,.heading h2{font-size:2.5em;font-weight:800;padding-top:2em}.heading h2{font-size:2em;margin-bottom:1em}.heading span{font-size:1.3em;font-weight:500}@-webkit-keyframes slide_down{from{transform:translateY(0px)}to{transform:translateY(1em)}}@keyframes slide_down{from{transform:translateY(0px)}to{transform:translateY(1em)}}

.shop{max-height:80vh;text-align:center}.shop img{height:60vh}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 10px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

